     /* Container styling */
     .about-us {
       font-family: Arial, sans-serif;
       max-width: 800px;
       margin: 0 auto;
       padding: 20px;
     }
  
     .about-us h1 {
       text-align: center;
       margin-bottom: 30px;
       color: #3b3b3b;
     }
  
     .about-us-section {
       margin-bottom: 20px;
     }
  
     .about-us-section h2 {
       font-size: 24px;
       color: #2c3e50;
     }
  
     .about-us-section p {
       font-size: 16px;
       line-height: 1.6;
       color: #555;
     }
  
     .about-us-section ul {
       list-style-type: disc;
       padding-left: 20px;
     }
  
     .about-us-section li {
       font-size: 16px;
       color: #555;
     }
  
     .product-grid {
       display: grid;
       grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
       gap: 20px;
       padding: 20px;
     }
  
     .product-card {
       border: 1px solid #ddd;
       border-radius: 5px;
       padding: 15px;
       box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
       transition: box-shadow 0.3s ease-in-out;
       overflow: hidden;
       
     }
  
     .product-card img {
       width: 100%;
       height: auto;
       border-radius: 5px;
       display: block;
     }
  
     .product-card h2 {
       font-size: 1.5rem;
       margin: 10px 0;
     }
  
     .product-card p {
       font-size: 1rem;
       color: #777;
       margin: 5px 0;
     }
  
     .product-card button {
       padding: 10px 20px;
       background-color: #7c5b43;
       color: white;
       border: none;
       border-radius: 5px;
       cursor: pointer;
     }
  
     .product-card button:hover {
       background-color: #5a3e2d;
     }
  
